.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: -43px;
  padding: 0;
  display: block;
  width: 24px;
  height: 16px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
  margin-top: 3px;
}
@media (min-width: 768px) {
